import React, {Fragment, useEffect, useState} from 'react';
import {useHistory} from 'react-router-dom';
import { uploadGame, getUser, clearData } from '../actions/users';
import {useDispatch, useSelector} from 'react-redux';
import Nav from './Nav';
import TutorialGif from './TurorialGif';

const Edit = () => {

    const history = useHistory();
    const dispacht = useDispatch();

    const videogames = useSelector((state)=> state.userReducer.gamesUser);
    const [game, setGame] = useState('nada');
    const [img, setImg] = useState('data:image/png;base64,');
    
    const [input, setInput] = useState({
        name: "",
        img: "",
        description: "",
        rating: "",
        userId: localStorage.getItem('id')
    })
    
    function handleChange (e) {
        setInput({
            ...input,
            [e.target.name]: e.target.value
        });
        console.log(input)
        // console.log(e);
    }
    
    //realizador de nuevo videogame
    function handleSubmit (e) {
        e.preventDefault();
        const token = localStorage.getItem('token')
        const idGame = localStorage.getItem('idGame')
        const userId = localStorage.getItem('id')
        let n = game ? game.name : "cle";
        let d = game ? game.description : "cle";
        let i = game ? game.img : "cle";
        let r = game ? game.rating : "cle";
        let data = {
            name: input.name === ''? n : input.name,
            description: input.description === ''? d : input.description,
            img: input.img === ''? i : input.img,
            rating: input.rating === ''? r : input.rating,
        }
        console.log(data);
        console.log("Enviar")
        dispacht( uploadGame(idGame, data, token) );
        setTimeout(()=>{
            dispacht(clearData());
            localStorage.removeItem('idGame')
        }, 200)
        setTimeout(()=>{
            dispacht(getUser(userId));
        }, 500)
        history.push('/dashboard')
    }

    function buttonLinkImg(e){
        e.preventDefault()
        setImg(input.img)
        console.log('mostrare')
    }

    useEffect(()=>{
        const isUser = localStorage.getItem('payload');
        const idGame = localStorage.getItem('idGame');
        // console.log(videogames)
        if(isUser !== 'user' && !idGame) {
            return history.push('/login')
        } else {
            const idG = localStorage.getItem('idGame');
            const filterGame = videogames.filter(el => el.id === idG)
            let g = filterGame.map((el)=>{
                return {
                    id: el.id,
                    name: el.name,
                    description: el.description,
                    rating: el.rating,
                    img: el.img
                }
            })
            setGame(g[0])
            
        }
    }, [dispacht, history, videogames])


    return (
        <Fragment>
            <Nav current="New Game"/>
            {/* <div className='formParent'>
                <div className='contForm'> */}
            <div className="container mt-5">
                <div className="row d-flex justify-content-center">
                    <div className='col-md-5 col-sm-8'>

                    <form 
                        onSubmit={(e) => handleSubmit(e)}
                    >
                        <br />
                        <div className='containerInput'>

                            <p>Name</p>
                            <input 
                                onChange={handleChange}
                                type="text"
                                name= "name"
                                placeholder={
                                    game?
                                    game.name
                                    :
                                    "name"
                                }
                            />
                        </div>

                        <div className='containerInput'>

                            <p>Description</p>
                            <textarea 
                                rows="4" cols="55"
                                onChange={handleChange}
                                type="text"
                                name= "description"
                                placeholder={
                                    game?
                                    game.description
                                    :
                                    "description"
                                }
                            />
                        </div>
                        <hr/>
                        <div className='containerInput rounded-3 border border-secondary border-3 p-4'>

                            <p>Link-Img</p>
                            <div className='text-center'>
                                <TutorialGif/>
                            </div>
                            <input 
                                onChange={handleChange}
                                type="text"
                                name= "img"
                                placeholder={
                                    game?
                                    game.img
                                    :
                                    "img"
                                }
                            /><br/>
                            <button className='btn btn-secondary btn-sm' onClick={buttonLinkImg}>
                                upload image
                            </button>
                            {/* <input type="checkbox" name="Show" id="" /> */}
                            <div className="containerGame m-4">
                                <img className='w-50 rounded mx-auto d-block' src={img} alt="this-image-link-does-not-work" />
                            </div>
                        </div>

                        <div className='containerInput'>

                            <p>Rating</p>
                            <input 
                                onChange={handleChange}
                                type="text"
                                name= "rating"
                                placeholder={
                                    game?
                                    game.rating
                                    :
                                    "rating"
                                }
                            />
                        </div>
                        <div className='text-center mt-4'>
                            <button className='btn btn-success' type='submit'>Save</button>
                        </div>
                        <br /> <br />

                    </form>

                    </div>


                </div>
                </div>

                {/* </div>
            </div> */}
        </Fragment>
    )
}

export default Edit;